import Loadable from 'react-loadable';
import Loading from '../components/shared/loading';

const Home = Loadable({
  loader: () => import('../containers/home'),
  loading: Loading,
  webpack: () => [require.resolveWeak('../containers/home')]
});

const RoomStatus = Loadable({
  loader: () => import('../containers/room-status'),
  loading: Loading,
  webpack: () => [require.resolveWeak('../containers/room-status')]
});

const HotelConfig = Loadable({
  loader: () => import('../containers/hotel-config'),
  loading: Loading,
  webpack: () => [require.resolveWeak('../containers/hotel-config')]
});

const routes = [
  {
    path: '/',
    component: Home,
    routes: [
      {
        path: '/status',
        component: RoomStatus
      },
      {
        path: '/config',
        component: HotelConfig
      }
    ]
  }
];

export default routes;
